window.addEventListener("load", function () {
    const elBtnCategories = $('#projects .categories button');
    const elSwiperSlide = $('.mySwiper .swiper-slide');

    // Khi to Swiper v lu instance
    const swiper = new Swiper(".mySwiper", {
        slidesPerView: 1,
        pagination: {
            el: ".swiper-pagination",
        },

        breakpoints: {
            768: {
                slidesPerView: 3,
            }
        }
    });

    // ---  m s lng slide theo tng class ---
    function updateCounts() {
        const counts = {};

        // m tng className
        elSwiperSlide.each(function () {
            const className = $(this).data('classname');
            counts[className] = (counts[className] || 0) + 1;
        });

        // Cp nht vo tng nt
        elBtnCategories.each(function () {
            const $btn = $(this);
            const className = $btn.attr('class').split(' ')[0]; // ly class u

            if (className === 'all') {
                const total = elSwiperSlide.length;
                $btn.find('span').text(total);
            } else {
                $btn.find('span').text(counts[className] || 0);
            }
        });
    };
    // ---  Click filter ---
    elBtnCategories.on('click', function (e) {
        e.preventDefault();

        const className = $(this).attr('class').split(' ')[0];
        elBtnCategories.removeClass('active');
        $(this).addClass('active');

        // --- n ht trc ---
        elSwiperSlide.css('display', 'none');
        setTimeout(() => {
            // --- Ri mi show nhm cn ---
            if (className === 'all') {
                elSwiperSlide.css('display', 'flex');
            } else {
                elSwiperSlide.filter('[data-classname="' + className + '"]').css('display', 'flex');
            };
            swiper.update(); // Cp nht li Swiper
            if (swiper.lazy) {
                swiper.slides.each(function(index, slide) {
                    if ($(slide).css('display') !== 'none') {
                        swiper.lazy.loadInSlide(index);
                    }
                });
            }
        }, 50);
    });

    updateCounts(); // Gi khi load trang
    setTimeout(() => {
        elBtnCategories.eq(0).trigger('click');
    }, 500);


});
